import { BookingStatusCode } from '../enums/BookingStatusCode';
import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Booking
 *  A booking is a pending request to book a resource for specific time for user(s), once it is approved, it becomes a placement
*/
export class Booking extends BaseEntity {
 
    // When to ask for placement [Epoch milliseconds Timestamp] 
    public placementOn: number;
 
    // Booking for specific resource 
    public resourceId: string;
 
    // Booking request Id 
    public requestId: string;
 
    // User Id who requested the booking 
    public requestedBy: string;
 
    // User Id for the placement 
    public requestedFor: string;
 
    // When the placement was approved [Epoch milliseconds Timestamp] 
    public approvedOn: number;
 
    // Booking status 
    public status: BookingStatusCode;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Booking
*/
export interface IBooking extends IBaseEntity {
 
    // When to ask for placement [Epoch milliseconds Timestamp] 
    placementOn?: number;
 
    // Booking for specific resource 
    resourceId?: string;
 
    // Booking request Id 
    requestId?: string;
 
    // User Id who requested the booking 
    requestedBy?: string;
 
    // User Id for the placement 
    requestedFor?: string;
 
    // When the placement was approved [Epoch milliseconds Timestamp] 
    approvedOn?: number;
 
    // Booking status 
    status?: BookingStatusCode;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
